import {useFormik} from 'formik';
import styled from "styled-components";
import emailjs from '@emailjs/browser';
import React, {useState} from 'react';


const FormStyle = styled.form`
  display: flex;
  flex-direction: column;
  text-align: left;
  gap: 0.5em;
`;
const ErrorStyle = styled.div`
  color: red;
`;
const InputStyle = styled.input`
`;

const validate = values => {
    const errors = {};
    if (!values.firstName) {
        errors.firstName = ' ';
    } else if (values.firstName.length > 15) {
        errors.firstName = '    15 .';
    }

    if (!values.lastName) {
        errors.lastName = ' ';
    } else if (values.lastName.length > 20) {
        errors.lastName = '    20 .';
    }

    if (!values.email) {
        errors.email = ' Email';
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
        errors.email = 'Email  ';
    }

    return errors;
};

const SignupForm = () => {
    const [buttonState, setButtonState] = useState('');
    const formik = useFormik({
        initialValues: {
            firstName: '',
            lastName: '',
            email: '',
        },
        validate,
        onSubmit: (object, {resetForm}, setStatus) => {
            emailjs.send(process.env.REACT_APP_Service, process.env.REACT_APP_TemplateId, object, process.env.REACT_APP_PublicKey)

                .then((result) => {
                    console.log(result.text)
                    setButtonState(' ')
                    resetForm();
                    setTimeout(() => {
                        setButtonState('')
                    }, 3000);

                }, (error) => {
                    console.log(error.text)
                    setButtonState(',    ')
                    resetForm();
                })
        },
    });
    return (
        <>
            <h2>       !</h2>
            <FormStyle onSubmit={formik.handleSubmit}>
                <label htmlFor="firstName"></label>
                <InputStyle
                    id="firstName"
                    name="firstName"
                    type="text"
                    onChange={formik.handleChange}
                    value={formik.values.firstName}
                />
                {formik.errors.firstName ? <ErrorStyle>{formik.errors.firstName}</ErrorStyle> : null}

                <label htmlFor="lastName"> </label>
                <InputStyle
                    id="lastName"
                    name="lastName"
                    type="text"
                    onChange={formik.handleChange}
                    value={formik.values.lastName}
                />
                {formik.errors.lastName ? <ErrorStyle>{formik.errors.lastName}</ErrorStyle> : null}

                <label htmlFor="email">Email </label>
                <InputStyle
                    id="email"
                    name="email"
                    type="email"
                    onChange={formik.handleChange}
                    value={formik.values.email}
                />
                {formik.errors.email ? <ErrorStyle>{formik.errors.email}</ErrorStyle> : null}
                <button type="submit"></button>
                <ErrorStyle>{buttonState}</ErrorStyle>

            </FormStyle>
        </>
    );
};

export default SignupForm;