import styled from 'styled-components';
import ReactEmail from '../componets/formic';
import {AnimationOnScroll} from 'react-animation-on-scroll';
import {FadeIn} from "react-slide-fade-in";


import Icon1 from '../img/icon1.png'
import Icon2 from '../img/icon2.png'
import Icon3 from '../img/icon3.png'
import Icon4 from '../img/icon4.png'
import Headerimg from '../img/background.jpg'
import Footerimg from '../img/background2.jpg'

import ContMassage from '../componets/contmassage'

import YandexMap from '../componets/yandexmap'
import {NavLink} from "react-router-dom";
import React from "react";

const AboutBack = styled.div`
  background-image: url(${Footerimg});
  background-attachment: fixed;
  background-size: cover;
  background-position: center;

`;
const About = styled.div`
  background-color: #fff8eb;
  display: grid;
  grid-template-columns: 1fr 1fr;
  @media screen and  (max-width: 960px) {
    grid-template-columns:  1fr;
  };
`;

const AboutText = styled.div`
  margin: 2em;
  padding: 2em;
`;

const AboutTextUL = styled.ul`
  text-align: left;
`;

const AboutKont = styled.div`
  border: 6px solid #7ba80a;
  border-radius: 20px;
  margin: 2em;
  padding: 2em;
`;

const AboutMap = styled.div`
  margin: 2em;
  padding: 2em;
`;

const Address = styled.footer`
  display: flex;
  flex-direction: column;
  background: white;
  padding: 1em;
  max-width: 250px;
  position: absolute;
  top: 40px;
  left: 40px;
  opacity: 0.8;
  border-radius: 20px;
  @media screen and  (max-width: 640px) {
    position: static;
    max-width: 650px;
  };
`;

const Works = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  justify-items: center;

  @media screen and  (max-width: 960px) {
    grid-template-columns: 1fr 1fr;
  };
`;

const Work = styled.div`
  width: min-content;
  text-align: center;
  margin: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const WorkDiv = styled.div`
  display: flex;
  justify-content: center;
  border: 0.4em solid #84be19;
  border-radius: 50%;
  box-shadow: 0px 0px 0px 0.4em rgba(222, 222, 222, 0.75) inset;
  margin: 1em;
  max-width: 250px;
`;

const WorkImg = styled.img`
  max-width: 150px;
  min-width: 20px;
  padding: 2em;
  @media screen and  (max-width: 1170px) {
    max-width: 120px;
  };
  @media screen and  (max-width: 1050px) {
    max-width: 100px;
  };
  @media screen and  (max-width: 960px) {
    max-width: 150px;
  };
  @media screen and  (max-width: 640px) {
    max-width: 80px;
  };
  @media screen and  (max-width: 500px) {
    max-width: 50px;
  };
  @media screen and  (max-width: 500px) {
    max-width: 30px;
  };
  @media screen and  (max-width: 350px) {
    max-width: 20px;
  };
`;


const IndexPage = () => (
    <>
        <div className='headerBack'>
            <img src={Headerimg} alt=" "/>
        </div>
        <div className='homePage'>
            <Works>

                <FadeIn
                    from="bottom"
                    positionOffset={0}
                    triggerOffset={0}
                    delayInMilliseconds={200}
                >
                    <NavLink as={NavLink} to={"/development"}>
                        <Work>
                            <WorkDiv>
                                <WorkImg src={Icon1} alt=""/>
                            </WorkDiv>
                                
                        </Work>

                    </NavLink>
                </FadeIn>

                <FadeIn
                    from="bottom"
                    positionOffset={0}
                    triggerOffset={0}
                    delayInMilliseconds={200}
                >
                    <NavLink as={NavLink} to={"/reporting"}>

                        <Work>
                            <WorkDiv>
                                <WorkImg src={Icon2} alt=""/>
                            </WorkDiv>
                             
                        </Work>
                    </NavLink>
                </FadeIn>

                <FadeIn
                    from="bottom"
                    positionOffset={0}
                    triggerOffset={0}
                    delayInMilliseconds={200}
                >
                    <NavLink as={NavLink} to={"/additionall"}>
                        <Work>
                            <WorkDiv>
                                <WorkImg src={Icon3} alt=""/>
                            </WorkDiv>
                             
                        </Work>
                    </NavLink>
                </FadeIn>
                <FadeIn
                    from="bottom"
                    positionOffset={0}
                    triggerOffset={0}
                    delayInMilliseconds={200}
                >
                    <NavLink as={NavLink} to={"/training"}>
                        <Work>
                            <WorkDiv>
                                <WorkImg src={Icon4} alt=""/>
                            </WorkDiv>

                             
                        </Work>
                    </NavLink>
                </FadeIn>

            </Works>

            <AboutBack>

                <About>

                    <AboutText>
                        <AnimationOnScroll animateOnce={true} duration={0.3} offset={100} animateIn='animate__zoomIn'>
                            <h3> </h3>
                            <h4><b>  ""      .</b></h4>
                            <p><b>    
                                   <u></u>  <u> 
                                    &raquo;.</u> </b></p>
                            <p><b> :</b></p>
                            <AboutTextUL>
                                <li><b> &nbsp; &nbsp;  ,
                                     
                                        </b></li>
                                <li><b>      </b></li>
                                <li>
                                    <b>    </b>
                                </li>
                            </AboutTextUL>
                        </AnimationOnScroll>
                    </AboutText>


                    <AboutKont>
                        <AnimationOnScroll animateOnce={true} duration={0.3} offset={100} animateIn='animate__zoomIn'>
                            <ReactEmail/>
                            <ContMassage/>
                        </AnimationOnScroll>
                    </AboutKont>

                </About>

                <AboutMap>
                    <AnimationOnScroll duration={0.7} animateIn='animate__fadeInLeftBig'>
                        <Address>
                            <p> :</p>
                            <p> ,  , . 7, . 15</p>
                        </Address>
                    </AnimationOnScroll>

                    <YandexMap/>
                </AboutMap>

            </AboutBack>

        </div>
    </>
);

export default IndexPage;